body {
  background: #fa9be5;
  justify-content: center;
  align-items: center;
  margin: 20px;
  display: flex;
}

.loader {
  color: #176497;
  font-size: 18px;
  animation: swing 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

@keyframes swing {
  0% {
    transform: translateX(-50%)translateY(-50%)translateX(0);
  }

  50% {
    transform: translateX(-50%)translateY(-50%)translateX(30px);
  }

  100% {
    transform: translateX(-50%)translateY(-50%)translateX(0);
  }
}

.cat-info {
  margin-top: 20px;
  padding: 20px;
}

.error {
  color: red;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, .2);
  font-size: 18px;
  font-weight: bold;
}

.breed-select {
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  float: left;
  margin-bottom: 10px;
  margin-right: 20px;
}

.cat-info p {
  margin: 12px 0 0;
  font-size: 16px;
  line-height: 1.6;
}

.cat-container {
  align-items: center;
  display: flex;
}

